<template>
  <div class="wrapper flex-1" ref="wrapper" @dblclick="boxReset">
    <div class="box" ref="box">
      <img :src="url" ref="img" @load="loadImage()">
    </div>
  </div>
</template>

<style lang="scss" scoped>
.wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  top: 0;
  left: 0;
  overflow: hidden;
  background: #ccc;
}
.box {
  position: absolute;
}
</style>

<script>
import {addWheelListener, removeWheelListener} from 'wheel'
import url from '!file-loader!./tmp.png'
import Dragger from 'draggabilly'

export default {
  data() {
    const baseUrl = process.env.NODE_ENV === 'production' ? '../tmp.png' : url
    return {
      img: {
        realWidth: null,
        realHeight: null,
        width: null,
        height: null,
      },
      center: {
        left: null,
        top: null,
      },
      baseUrl,
      url: baseUrl,
    }
  },
  methods: {
    firstLoadedImage() {
      this.img.realWidth = this.img.width = this.$refs.img.width
      this.img.realHeight = this.img.height = this.$refs.img.height
      this.img.whRate = this.img.width / this.img.height
      this.bindEvent()
      this.boxReset()
    },
    boxReset() {
      let $wrapper = this.$refs.wrapper
      if ($wrapper.clientWidth / $wrapper.clientHeight > this.img.whRate) {
        this.boxResize({height: $wrapper.clientHeight})
      } else {
        this.boxResize({width: $wrapper.clientWidth})
      }
      this.boxCenter()
    },
    boxResize({width = null, height = null} = {}) {
      let $box = this.$refs.box
      let $img = this.$refs.img
      if (width) {
        this.img.width = width
        this.img.height = width / this.img.whRate
      } else if (height) {
        this.img.height = height
        this.img.width = height * this.img.whRate
      }
      $img.width = $box.width = this.img.width
      $img.height = $box.height = this.img.height
    },
    boxCenter({top = 0.5, left = 0.5} = {}) {
      if (top < 0) {
        top = 0
      }
      if (top > 1) {
        top = 1
      }
      if (left < 0) {
        left = 0
      }
      if (left > 1) {
        left = 1
      }
      let $wrapper = this.$refs.wrapper
      let $box = this.$refs.box
      this.center.left = left
      this.center.top = top
      $box.style.top = ($wrapper.clientHeight/2 - top*this.img.height) + 'px'
      $box.style.left = ($wrapper.clientWidth/2 - left*this.img.width) + 'px'
    },
    bindEvent() {
      let $wrapper = this.$refs.wrapper
      let $box = this.$refs.box
      var dragger = new Dragger($box)
      dragger.on('dragEnd', (event) => {
        let {top, left} = $box.style
        top = parseFloat(top)
        left = parseFloat(left)
        this.center.top = ($wrapper.clientHeight/2 - top)/this.img.height
        this.center.left = ($wrapper.clientWidth/2 - left)/this.img.width
      })
      addWheelListener($wrapper, e => {
        e.preventDefault()
        let {top, left} = this.center
        if (e.wheelDelta > 0) {
          this.boxResize({width: this.img.width * 1.1})
          this.boxCenter({top, left})
        } else {
          this.boxResize({width: this.img.width * 0.9})
          this.boxCenter({top, left})
        }
      })
    },
    loadImage() {
      this.$emit('loadedImage')
    },
    reloadImage() {
      let url = this.baseUrl + '?' + Date.now()
      let img = new Image()
      img.src = url
      img.addEventListener('load', () => {
        this.url = url
      })
    },
  },
  mounted() {
    this.$once('loadedImage', this.firstLoadedImage.bind(this))
    if (this.$refs.img.complete) {
      this.$emit('loadedImage')
    }
    setInterval(() => {
      this.reloadImage()
    }, 1000)
  },
}
</script>



